body {
  height: 100vh;
  background: rgb(34, 193, 195);
  background: linear-gradient(0deg, rgba(34, 193, 195, 1) 0%, rgba(253, 187, 45, 1) 100%);
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.clock {
  position: relative;
  margin: 8% auto 0;
  width: 300px;
  height: 300px;
  background: #ececec;
  border: 14px solid #333;
  border-radius: 50%;
  box-shadow: 0 2vw 4vw -1vw rgba(0, 0, 0, 0.8);
}

.dot {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 14px;
  height: 14px;
  background-color: #ccc;
  border-radius: 50%;
  box-shadow: 0 2px 4px -1px black;
  z-index: 10;
}

.hour-hand,
.minute-hand,
.second-hand {
  position: absolute;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;

}

.hour-hand {
  position: absolute;
  top: 79px;
  left: 50%;
  margin-left: -2px;
  width: 6px;
  height: 65px;
  background-color: rgb(100, 99, 99);
  transform-origin: 50% 72px;
  z-index: 5;

}

.minute-hand {
  top: 46px;
  left: 50%;
  margin-left: -2px;
  width: 4px;
  height: 100px;
  background-color: #666;
  transform-origin: 50% 105px;
  z-index: 6;

}

.second-hand {
  top: 26px;
  left: 50%;
  margin-left: -1px;
  width: 2px;
  height: 120px;
  background-color: red;
  transform-origin: 50% 125px;
  z-index: 4;

}

.division>span {
  position: absolute;
  display: inline-block;
  color: #333;
  font-size: 22px;
  font-family: 'Poiret One';
  font-weight: 700;
  z-index: 4;
}

.h12 {
  top: 30px;
  left: 50%;
  margin-left: -11px;
}

.h3 {
  top: 140px;
  right: 30px;
}

.h6 {
  bottom: 30px;
  left: 50%;
  margin-left: -5px;
}

.h9 {
  top: 140px;
  left: 32px;
}

.diallines {
  position: absolute;
  left: 50%;
  width: 2px;
  height: 15px;
  margin-left: -1px;
  background-color: #666;
  transform-origin: 50% 150px;
  z-index: 2;
}

.diallines:nth-of-type(5n) {
  position: absolute;
  left: 50%;
  width: 4px;
  height: 25px;
  margin-left: -2px;
  background-color: #666;
  transform-origin: 50% 150px;
  z-index: 2;

}

.info {
  position: absolute;
  top: 200px;
  left: 50%;
  height: 30px;
  margin-left: -60px;
  font-family: 'Poiret One';
  font-size: 1em;
  font-weight: 700;
  text-align: center;
  color: #000;
  line-height: 30px;
  transform: translateX(-10%);

}

.date {
  top: 80px;
  width: 150px;

}

.day {
  width: 200px;
  transform: translateX(-20%);
  border-radius: 7px;
}

footer>p {
  padding: 20px;
  font-size: 1.2em;
  color: #f1f1f1;
  text-align: center;
}